/* eslint-disable */
import type { ChoiceEvent } from '../models/ChoiceEvent';
import type { CompletionEvent } from '../models/CompletionEvent';
import type { CompletionRequest } from '../models/CompletionRequest';
import type { CompletionResponse } from '../models/CompletionResponse';

import type { CancelablePromise } from '../core/CancelablePromise';
import type { BaseHttpRequest } from '../core/BaseHttpRequest';

export class DefaultService {

  constructor(public readonly httpRequest: BaseHttpRequest) {}

  /**
   * Completions
   * @param requestBody
   * @returns CompletionResponse Successful Response
   * @throws ApiError
   */
  public completionsV1CompletionsPost(
    requestBody: CompletionRequest,
  ): CancelablePromise<CompletionResponse> {
    return this.httpRequest.request({
      method: 'POST',
      url: '/v1/completions',
      body: requestBody,
      mediaType: 'application/json',
      errors: {
        422: `Validation Error`,
      },
    });
  }

  /**
   * Events
   * @param requestBody
   * @returns any Successful Response
   * @throws ApiError
   */
  public eventsV1EventsPost(
    requestBody: (ChoiceEvent | CompletionEvent),
  ): CancelablePromise<any> {
    return this.httpRequest.request({
      method: 'POST',
      url: '/v1/events',
      body: requestBody,
      mediaType: 'application/json',
      errors: {
        422: `Validation Error`,
      },
    });
  }

}
